import { createTheme } from "@material-ui/core";
import typography from "./typography";

const baseTheme = {
    breakpoints: {
        values: {
            xs: 0,
            sm: 700,
            md: 960,
            lg: 1320,
            xl: 1440,
            xxl: 1920,
        },
    },
    navbarHeight: "100px",
    mobileNavbarHeight: "55px",
    typography,
    overrides: {
        MuiButton: {
            root: {
                textTransform: "none",
                fontSize: "16px",
            },
            text: {
                letterSpacing: "2px",
                borderRadius: 0,
                "&:hover": {
                    color: "rgb(230,230,230)",
                    backgroundColor: "inherit",
                },
            },
        },
    },
};

const darkTheme = createTheme({
    palette: {
        background: {
            default: "#0D0D0D",
        },
        primary: {
            main: "#FFF",
        },
        secondary: {
            main: "#E4E4E4",
        },
        text: {
            primary: "#FFF",
            secondary: "#969696",
            tertiary: "#C8C8C8",
        },
        action: {
            disabled: "#E4E4E4",
            disabledBackground: "#969696",
        },
    },
    logoColor: {
        primary:"#E4E4E4",
        secondary:"#FFF",
    },
    ...baseTheme
});

const lightTheme = createTheme({
    palette: {
        background: {
            default: "#FFF",
        },
        primary: {
            main: "#CBAA36",
        },
        secondary: {
            main: "#E4E4E4",
        },
        text: {
            primary: "#000",
            secondary: "#1E1E1E",
            tertiary: "#000",
        },
        action: {
            disabled: "#1E1E1E",
            disabledBackground: "#969696",
        },
    },
    logoColor: {
        primary:"#CBAA36",
        secondary:"#EEC843",
    },
    ...baseTheme
});

export { darkTheme, lightTheme };
