import { useState, useEffect, Suspense } from "react";
import { ThemeProvider } from "@material-ui/styles";
import { darkTheme, lightTheme } from "./assets/theme";
import CssBaseline from "@material-ui/core/CssBaseline";
import "./App.css";
import ScrollToTop from "./components/ScrollToTop";
import { default as scroll } from "silky-smooth-scroll";
import { BrowserRouter as Router } from "react-router-dom";
import Routes from "./Routes";
import ThemeContext from "./contexts/themeContext";
import LoaderContext from "./contexts/loaderContext";
import { Helmet } from 'react-helmet';

function App() {

    // Retrieve the theme preference from localStorage
    const [isDarkMode, setIsDarkMode] = useState(() => {
        const savedTheme = localStorage.getItem('isDarkMode');
        return savedTheme !== null ? JSON.parse(savedTheme) : true; // Set this to false to default to light mode
    });

    const [isLoading, setIsLoading] = useState(true); // Set this to true to show the loader (set to true also in loaderContext.js and uncomment in Routes.js)

    // Save the theme preference to localStorage whenever it changes
    useEffect(() => {
        localStorage.setItem('isDarkMode', JSON.stringify(isDarkMode));
    }, [isDarkMode]);

    if (typeof window !== "undefined") {
        //scroll({ speed: 50, smooth: 40, smoothEdgeStop: false });
        scroll({ speed: 50, smooth: 30, smoothEdgeStop: true });
    }

    /*
    useEffect(() => {
        function handleMatchMediaChange(e) {
          if (e.matches) {
            setIsDarkMode(true);
          } else {
            setIsDarkMode(false);
          }
        }
    
        if (window.matchMedia) {
          const mediaQueryList = window.matchMedia('(prefers-color-scheme: dark)');
          mediaQueryList.addListener(handleMatchMediaChange); // Listen for changes
    
          // Initial check
          handleMatchMediaChange(mediaQueryList);
    
          // Cleanup listener on unmount
          return () => mediaQueryList.removeListener(handleMatchMediaChange);
        }
    }, []);
    */

    return (
        <>
            <Helmet>
                <meta name="robots" content="noai, noimageai" />
            </Helmet>
            <Router>
                <Suspense fallback={<div></div>}>
                    <ThemeContext.Provider value={{ isDarkMode, setIsDarkMode }}>
                        <LoaderContext.Provider value={{ isLoading, setIsLoading }}>
                            <ThemeProvider theme={isDarkMode ? darkTheme : lightTheme}>
                                <CssBaseline />
                                <ScrollToTop />
                                <Routes />
                            </ThemeProvider>
                        </LoaderContext.Provider>
                    </ThemeContext.Provider>
                </Suspense>
            </Router>
        </>
    );
}

export default App;
