import React, { lazy, useState, useEffect } from "react";
import { useMediaQuery, useTheme } from "@material-ui/core";
import { Switch, Route } from "react-router-dom";
const MainHome = lazy(() => import("./pages/MainHome"));
const Immersive = lazy(() => import("./pages/Immersive"));
const Film = lazy(() => import("./pages/Film"));
const Design = lazy(() => import("./pages/Design"));
const Navbar = lazy(() => import("./components/Navbar"));
const Social = lazy(() => import("./components/Social"));
const Footer = lazy(() => import("./components/Footer"));
const Loader = lazy(() => import("./components/Loader"));

const Routes = () => {
    const theme = useTheme();
    const isMobileOrTablet = useMediaQuery(theme.breakpoints.down("md"));
    const [isScrolledToEnd, setIsScrolledToEnd] = useState(false);

    const handleScroll = () => {
        const scrollTop = window.scrollY;
        const windowHeight = window.innerHeight;
        const documentHeight = document.documentElement.scrollHeight;
    
        if (scrollTop + windowHeight + 100 >= documentHeight) {
            setIsScrolledToEnd(true);
        } else {
            setIsScrolledToEnd(false);
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
          window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <>
            <Loader /> {/* Uncomment this and set isLoading to true in loaderContext.js and App.js to show the loader */}
            <Navbar />
            {(!isMobileOrTablet && !isScrolledToEnd) && <Social />}
            
            <Switch>
                <Route exact path="/" component={MainHome} />
                <Route exact path="/immersive" component={Immersive} />
                <Route exact path="/film" component={Film} />
                <Route exact path="/design" component={Design} />
            </Switch>
            
            <Footer />
        </>
    );
};

export default Routes;
